import React, { Component } from "react";
import base from "./Assets/img/base.jpg";
import Voices from "./Voice";

export class MagicConch extends Component {
    constructor() {
        super();
        this.voices = new Voices();
        this.state = {showText: false};
        this.getQuote = this.getQuote.bind(this);
        this.option = 
        [
          ["Maybe someday.", this.voices.maybesomeday1],
          ["I don't think so.", this.voices.idontthinkso1],
          ["No.", this.voices.no1, this.voices.no2, this.voices.no3],
          ["Yes.", this.voices.yes1],
          ["Try asking again.", this.voices.tryaskingagain1]
        ]
        this.reply= "";
        this.noHist=0;
    }

    weightedRandom(prob) {
        let i, sum=0, r=Math.random();
        for (i in prob) {
          sum += prob[i];
          if (r <= sum) return i;
        }
    }

    getModifiedResponse() {
        // 0 = Yes/No 60
        // 1 = I dont think So, maybe someday 30
        // 2 = Try again 10
        let textChoice = this.weightedRandom({0:0.8, 1:0.15, 2:0.05});
        let voiceChoice = 1;
        console.log(textChoice);
        switch (textChoice) {
            case "0": {
                textChoice=this.weightedRandom({2:0.5, 3:0.5})
                if (textChoice === "2") {
                    if (this.noHist < 3) {
                        voiceChoice = this.noHist + 1;
                    }
                    else {
                        this.noHist = 0;
                    }
                    this.noHist += 1;
                }
                else {
                    this.noHist = 0;
                }
                break;
            }
            case "1": {
                textChoice=this.weightedRandom({0:0.5, 1:0.5})
                this.noHist = 0;
                break;
            }
            case "2": {
                textChoice=4
                this.noHist = 0;
                break;
            }
            default: break;
        }
        let response = [this.option[textChoice][0], this.option[textChoice][voiceChoice]];
        return response;
    }

    getQuote() {
        this.reply=this.getModifiedResponse();
        let audio = new Audio(this.reply[1])
        this.setState({showText: true});
        audio.play();
    }

    render() {
        return (
            <div>
                <div class="highlight">
                    <img src={base} 
                    alt="default" 
                    onClick={() => this.getQuote()}
                    />
                </div>
                {this.state.showText && (
                    <div class="reply">{this.reply[0]}</div>
                )
                }
            </div>
        )
    }
}